import React from 'react'

function Header() {
  return (
    <div>
      <header className="header-home text-center">
        <div className="container">
          <div className="row">
            <div className="col-md-12 mt-5">
              <h6>حان الوقت</h6>
            </div>
            <div className="col-md-12">
              <h1>
                اكتشف مهاراتك و استغل خبراتك
                <br />
                <br />
                داخل منصة
                <span> شغل</span>
              </h1>
            </div>
            <div className="col-md-12">
              <p>
                ستتمكن من عرض مواهبك والاستفاده منها بحيث تكون مصدر دخل اضافي لك
                و<br /> ستتمكن من الوصول لمشتغلين ينجزون لك اي اشغال تريد
                انجازها سوى الاشغال التي
                <br /> عن بعد او الاشغال التي عن قرب وذلك بعد ان تكتشف سعر السوق
                للخدمة التي تريدها
                <br /> ... وتختار المشتغل الانسب لك والجاهز لخدمتك
              </p>
            </div>

            <div className="col-md-12">
              <a href="#">ابحث عن مشتغلين</a>
              <img src='/Assets/img/Group20955.png' id='position-img'/>
              <a href="#">كن مشتغل وابداء سيرتك الذكية</a>
            </div>

            <div className="col-md-12">
              <img src='/Assets/img/Group 20947.png'/>
            </div>
          </div>
        </div>
      </header>
    </div>
  );
}

export default Header