import React from 'react'

function Explore() {
  return (
    <div>
      <main className="explore-home text-center mt-3">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <h1>اكتشف ميزاتنا الرئيسية</h1>
              <img src="/Assets/img/Group20965.png" />
              <p>
                افتح الإمكانات الكاملة لمهنتك المستقلة من خلال مجموعتنا الشاملة
                من الميزات. من مطابقة المشاريع الشخصية والعملاء الذين تم
                <br /> .التحقق منهم إلى المدفوعات الآمنة وترتيبات العمل المرنة،
                تم تصميم منصتنا لرفع مستوى تجربتك المستقلة
              </p>
            </div>
            <div className="col-md-4 mt-5 explore-content">
              <i
                className="fa-solid fa-paper-plane"
                style={{ color: "#657fbf" }}
              />
              <h3>التواصل في الوقت الفعلي</h3>
              <h4>
                ابق على اتصال مع العملاء أو المستقلين من خلال نظام المراسلة
                السلس الخاص بنا. ناقش تفاصيل المشروع، وقدم التحديثات، وتعاون
                بكفاءة - كل ذلك داخل المنصة.
              </h4>
            </div>

            <div className="col-md-4 mt-5 explore-content">
              <i
                className="fa-solid fa-user-check"
                style={{ color: "#657fbf" }}
              />
              <h3>العملاء والمستقلون الذين تم التحقق منهم</h3>
              <h4>
                ثقتك وسلامتك أمر بالغ الأهمية بالنسبة لنا. يتم التحقق بدقة من
                جميع العملاء والمستقلين على منصتنا لضمان بيئة عمل آمنة وموثوقة.
              </h4>
            </div>

            <div className="col-md-4 mt-5 explore-content ">
              <i
                className="fa-solid fa-earth-americas"
                style={{ color: "#657fbf" }}
              />
              <h3>مجموعة واسعة من المشاريع</h3>
              <h4>
                اكتشف مجموعة متنوعة من المشاريع عبر مختلف المجالات والمهارات. من
                تطوير الويب إلى التصميم الجرافيكي، والكتابة إلى التدريس الخصوصي
                والفنين والمقاولين - ولدينا تصنيف
                <br /> اي شغل
              </h4>
            </div>

            <div className="col-md-4 mt-5 explore-content">
              <img src="/Assets/img/time-twenty-four.png" id="star" />

              <h3>دعم العملاء على مدار الساعة طوال أيام الأسبوع</h3>
              <h4>
                هل لديك سؤال أو تحتاج إلى مساعدة؟ يتوفر فريق دعم العملاء
                المتفاني لدينا على مدار الساعة لتقديم مساعدة سريعة وودية.
              </h4>
            </div>

            <div className="col-md-4 mt-5 explore-content">
              <img src="/Assets/img/shield-check.png" id="star" />
              <h3>مدفوعات آمنة ومأمونة</h3>
              <h4>
                المدفوعات الخالية من القلق هي أولويتنا. نستخدم بوابة دفع آمنة
                لمعالجة جميع المعاملات، مما يضمن تعويضا دقيقا في الوقت المناسب
                عن عملك الشاق.
              </h4>
            </div>

            <div className="col-md-4 mt-5 explore-content">
              <img src="/Assets/img/star-comment-alt.png" id="star"/>
              <h3>مراجعات وتقييمات شفافة</h3>
              <h4>
                ردود الفعل الصادقة مهمة. يسمح نظام المراجعة والتصنيف الشفاف
                الخاص بنا للعملاء بتقييم أداء المستقلين والمستقلين لتقييم
                الاحتراف المهني للعملاء.
              </h4>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}

export default Explore