import React from "react";

function Yourjops() {
  return (
    <div>
      <section className="jop-home">
        <div className="container">
          <div className="row">
            <div className="col-md-6 text-right icon-image">
              <h1>
                جميع اشغالك عن بعد او بالحضور
                <br />
                <br /> في منصه واحدة
              </h1>
              <p>
                هل أنت مستعد لنقل حياتك المهنية المستقلة إلى آفاق جديدة؟ تقدم
                منصتنا عالما من الفرص المثيرة في انتظارك لاستكشافها. بصفتك
                مستقلا موهوبا، فأنت تمتلك مهارات فريدة مطلوبة في مختلف المجالات.
                من خلال قسم "اكتشاف المزيد من الطلبات"، نأتي بهذه الفرص في
                متناول يدك
              </p>
              <hr />

              <div className="col-md-12">
                <h6>توصيات المشروع الشخصية</h6>
                <img
                  src="/Assets/img/user-check-2.png"
                  className="mr-2 graduaion"
                />
                <h6 className="ml-5">تصفح الفئات الشائعة</h6>
                <img src="/Assets/img/apps.png" className="ml-2" />
              </div>

              <div className="col-md-12 ">
                <h6>نجاحك هو أولويتنا</h6>
                <img
                  src="/Assets/img/graduation-cap.png"
                  className="mr-5 graduaion"
                  id="graduation"
                />
                <h6 className="ml-5">فلاتر ذكية</h6>
                <img src="/Assets/img/filters.png" className="ml-2" />
              </div>
              <div className="col-md-12">
                <a href="#">عرض جميع الطلبات</a>
              </div>
            </div>

            <div className="col-md-6">
              <img src="/Assets/img/Rectangle 412.png" />
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Yourjops;
